.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  background-color: #fff;
  padding: 31px 0;
  transition: all 0.25s ease-in-out 0s;
  backface-visibility: hidden;
  @media (max-width: 992px) {
    padding: 21px 0; }
  @media (max-width: 767px) {
    padding: 6px 0; }
  .logo {
    width: 100%;
    display: inline-flex;
    align-items: center;
    &_image {
      img {
        width: 100%; } }
    &_description {
      width: 100%;
      display: block; } } }
